body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.time{
  font-size: 80px;
  width: 300px;
  margin-bottom: 0;
}

.text{
  font-size: 20px;
color: grey;
width: 300px;
margin-top: 0px;
display: flex;
justify-content: space-between;
}

.timer{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
height:80vh;
}

.timercontrol input {
cursor: pointer;
width: 200px;
height: 40px;
font-size: 20px;
 margin : 20px;
}

.millisecondsinput {
  width: 150px;
  height: 60px;
  margin-top : 10px;
 border : 3px solid lightseagreen ;
text-align: center;
font-size: 16px;
}
.label{

  font-size: 20px;
  color: grey;
}
